body{margin:0;padding:0;height:100vh;font-family:'Varela Round', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

#nav-container{display:flex;flex-direction:row;justify-content:space-between;height:75px}#nav-container #logo-left{height:100%;display:flex;align-items:center;justify-content:center;width:25vw}#nav-container #logo-left a{margin:0;padding:0 10px 0 10px;border-left:2px solid #f8f8f8;border-right:2px solid #f8f8f8;color:#f8f8f8;font-size:1.5rem;cursor:pointer;transition-duration:0.25s;text-decoration:none;text-transform:uppercase}#nav-container .navlink-container{height:100%;margin:0;padding:0;width:50vw;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center}#nav-container .navlink-container a{margin:5px 0 0 0;padding:5px 20px;position:relative;font-size:1.1rem;font-family:'Sunflower', sans-serif;font-weight:500;color:#f8f8f8;text-decoration:none;cursor:pointer;z-index:0}#nav-container .navlink-container a:before,#nav-container .navlink-container a:after{position:absolute;opacity:0;font-size:20px;bottom:5px;transition:all 0.3s}#nav-container .navlink-container a:before{content:'(';left:0px;-webkit-transform:translateX(10px);transform:translateX(10px)}#nav-container .navlink-container a:after{content:')';right:0px;-webkit-transform:translateX(-10px);transform:translateX(-10px)}#nav-container .navlink-container .is-active:before{content:'(';opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}#nav-container .navlink-container .is-active:after{content:')';opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}#nav-container .navlink-container a:hover:before{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}#nav-container .navlink-container a:hover:after{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}#nav-container .profile-container{width:25vw;height:100%;place-items:center}#nav-container .profile-container .login-button{margin:0;padding:0;padding:8px 30px 5px 30px;border:1px solid transparent;border-radius:10px;background:#f8f8f8;color:#333;font-size:1.15rem;cursor:pointer;transition-duration:0.25s;text-decoration:none}#nav-container .profile-container .login-button:hover,#nav-container .profile-container .login-button:focus{background:transparent;color:#f8f8f8;border:1px solid #f8f8f8}#nav-container .profile-container .is-active{color:#f8f8f8;background:transparent;border:1px solid #f8f8f8}@media screen and (max-width: 910px){#nav-container{display:grid}#nav-container #logo-left{margin:0;padding:0;height:75px;width:100vw;display:grid;place-items:center;background-color:black}#nav-container #logo-left a img{margin:0;padding:0;width:25px;height:auto;padding:10px;border:2px solid black;border-radius:50%}#nav-container #rightside-container{display:none}#nav-container{height:100px}}

.login-container{width:100vw;height:auto;margin-top:100px;display:grid;place-items:center}.login-container form{display:flex;flex-direction:column;justify-content:center;align-items:flex-start}.login-container form label{font-size:2.5rem}.login-container form input{margin-top:50px;padding-left:10px;border:none;border-bottom:1px solid grey;background-color:transparent;font-size:1.25rem}.login-container form input:focus{outline:none}.login-container form button{margin-top:50px;padding:10px 30px;border:1px solid #333;border-radius:10px;color:#f8f8f8;background-color:#333;font-size:1.5rem;cursor:pointer;transition-duration:0.25s}.login-container form button:hover{color:#333;background-color:#f8f8f8}.login-container form p{margin-top:25px}

.login-container{width:100vw;height:auto;margin-top:100px;display:grid;place-items:center}.login-container form{display:flex;flex-direction:column;justify-content:center;align-items:flex-start}.login-container form label{font-size:2.5rem}.login-container form input{width:40vw;height:50px;margin-top:50px;padding-left:10px;border:none;border-bottom:1px solid grey;background-color:transparent;font-size:1.25rem}.login-container form input:focus{outline:none}.login-container form h2{font-family:'Orbitron', 'sans-serif';margin-top:50px}.login-container form .name-container{margin:0;padding:0;width:40vw;display:flex;flex-direction:row;justify-content:space-between}.login-container form .name-container input{width:45%}.login-container form .bias-container{width:40vw;height:50px;display:flex;flex-direction:row;justify-content:space-around;align-items:center}.login-container form .bias-container input[type="radio"]{opacity:0;position:fixed;width:0}.login-container form .bias-container label{font-size:2.5vw;font-family:'Orbitron', 'sans-serif';padding:5px 15px;border:2px solid black;border-radius:10px;cursor:pointer}.login-container form .bias-container input[type="radio"]:checked+label{border:2px solid crimson;color:crimson}.login-container form button{margin-top:50px;padding:10px 30px;border:1px solid #333;border-radius:10px;color:#f8f8f8;background-color:#333;font-size:1.5rem;cursor:pointer;transition-duration:0.25s}.login-container form button:hover{color:#333;background-color:#f8f8f8}

#landing-page{width:100vw}#landing-page .particle-background{display:none;position:absolute;width:100vw;height:125vh}@media screen and (max-width: 910px){.particle-background{height:100vh}}

#infocarousel-container{margin-top:100px}#infocarousel-container #ic-description-container{width:80vw;left:0;right:0;margin:0 auto}#infocarousel-container #ic-description-container h1{margin:0;padding:0;margin-left:1vw;margin-top:50px;margin-bottom:25px;font-family:'Orbitron', 'sans-serif'}#infocarousel-container .info-carousel{margin-bottom:25px;width:80vw;height:40vw;border-radius:30px;box-shadow:rgba(17,12,46,0.15) 0px 48px 100px 0px}#infocarousel-container .info-carousel img{height:100vh;width:auto}#infocarousel-container .description{display:none}.swiper-button-prev{color:white;left:0;padding-left:25px}.swiper-button-next{color:white;right:0;padding-right:25px}@media screen and (max-width: 910px){#infocarousel-container #ic-description-container{width:90vw}#infocarousel-container #ic-description-container h1{font-size:1.5rem}#infocarousel-container .info-carousel{width:90vw;height:60vw}#infocarousel-container .info-carousel img{width:auto;height:100%}#infocarousel-container .description{display:block;width:80vw;left:0;right:0;margin:0 auto}#infocarousel-container .description h1{font-family:"Montserrat", 'sans-serif';font-size:1.75rem;font-weight:700}#infocarousel-container .description p{font-family:"Montserrat", 'sans-serif';font-size:0.85rem;letter-spacing:1px;margin:0;padding:0;font-weight:300}.swiper-button-next,.swiper-button-prev{display:none}}

#dashboard-title{margin:0;padding:0;margin-left:11vw;margin-top:100px;margin-bottom:25px;font-family:'Orbitron', 'sans-serif'}#dashboard-container{left:0;right:0;margin:auto;width:80vw;height:auto;display:flex;flex-wrap:wrap;flex-direction:row;justify-content:space-between;align-items:flex-start}#dashboard-container .Gifts{width:42.5vw;height:35vw;display:flex;flex-direction:column;justify-content:space-between}#dashboard-container .Gifts .top-user{width:100%;height:35%;display:grid;place-items:center;background-color:rgba(0,0,0,0.25);border-radius:30px;color:white}#dashboard-container .Gifts .top-user div{width:90%;height:70%;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center}#dashboard-container .Gifts .top-user div h1{font-size:3rem;padding:10px 20px;border:2px solid black;border-radius:50%}#dashboard-container .Gifts .top-user div .description{display:flex;flex-wrap:wrap;flex-direction:row;justify-content:center;align-items:center}#dashboard-container .Gifts .top-user div .description p{margin:0;padding:0;width:90%}#dashboard-container .Gifts .top-user div .description div{width:90%;height:17.5px;background-color:white;border-radius:20px}#dashboard-container .Gifts .whatever{width:100%;height:60%;display:grid;place-items:center;background-color:rgba(0,0,0,0.25);border-radius:30px}@media screen and (max-width: 910px){#dashboard-title{font-size:1.5rem;margin-left:6vw}#dashboard-container{width:90vw}#dashboard-container .Gifts{width:100%;height:auto}#dashboard-container .Gifts .top-user{height:35vw;margin-top:30px}#dashboard-container .Gifts .top-user div h1{font-size:2rem;padding:11.5px 17.5px;border:2px solid black;border-radius:50%}#dashboard-container .Gifts .top-user div .description p{font-size:0.65rem}#dashboard-container .Gifts .top-user div .description div{width:90%;height:12px;background-color:white;border-radius:20px}#dashboard-container .Gifts .whatever{height:50vw;margin-top:30px}}

#top-donations{display:grid;place-items:center;margin:0;padding:0;width:35vw;height:35vw;background-color:#f8f8f8;border-radius:30px;box-shadow:rgba(17,12,46,0.15) 0px 48px 100px 0px}#top-donations .chart-title-container{width:100%;display:grid;place-items:center}#top-donations .chart-title-container h3{margin:15px 0 0 0;padding:0;color:#4d4d4d;font-size:1.15rem;font-family:'Varela Round', sans-serif}#top-donations .chart-button-container{width:100%;height:auto;display:flex;flex-direction:row;justify-content:space-evenly;margin-bottom:10px}#top-donations .chart-button-container .see_chart_button{font-size:1.5rem;font-family:'Varela Round', sans-serif;margin:0;padding:10px 30px;border:1px solid black;border-radius:10px;background:#f8f8f8;cursor:pointer}#top-donations .chart-button-container .see_data_button{font-size:1.5rem;font-family:'Varela Round', sans-serif;color:white;margin:0;padding:10px 30px;border:none;border-radius:10px;background:#333;cursor:pointer}@media screen and (max-width: 910px){#top-donations{width:100%;min-height:400px}#top-donations .chart-title-container h3{font-size:1rem}#top-donations .chart-button-container .see_chart_button{font-size:1rem;border:1px solid black}#top-donations .chart-button-container .see_data_button{font-size:1rem}}@media screen and (max-width: 410px){#top-donations{min-height:375px}#top-donations .chart-title-container h3{font-size:0.75rem}#top-donations .chart-button-container{margin-bottom:7.5px}}

#donation-container{margin-top:100px}#donation-container #header-container{display:flex;width:80vw;left:0;right:0;margin:0 auto}#donation-container #header-container h1{width:400px;margin-top:0;margin-bottom:0;padding:0;margin-left:1vw;margin-right:auto;display:inline-block;font-family:'Orbitron', 'sans-serif'}#donation-container #header-container .sort{display:flex;margin-left:auto;padding-right:0px;justify-content:flex-end;align-items:flex-end}#donation-container #header-container .sort:hover{cursor:pointer;font-weight:bold}@media screen and (max-width: 910px){#donation-container #header-container{width:90vw}#donation-container #header-container h1{font-size:1.5rem}}

.each-pinned-gift-campaign{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%}.each-pinned-gift-campaign .img-container{width:85%;height:165px;background-size:cover;border:0.5px solid rgba(221,221,221,0.486);border-radius:5px;cursor:pointer}.each-pinned-gift-campaign .text-container{width:85%;cursor:pointer}.each-pinned-gift-campaign .text-container h4{margin:15px 0 0 0;padding:0;font-size:18px;font-weight:500;letter-spacing:2px}.each-pinned-gift-campaign .text-container p{color:#646464;font-size:12.5px;margin:10px 0 0 0;padding:0}.each-pinned-gift-campaign .text-container p .posted-user{background:none;color:#646464}.each-pinned-gift-campaign .description-container{width:85%;height:auto;margin:0;padding:0;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.each-pinned-gift-campaign .description-container p{margin:0;padding:0;color:#646464;font-size:10px}

.giftpinned-container{margin:50px 0 0 0;width:85vw;height:auto}.giftpinned-container h1{color:white}.giftpinned-container h1 span{font-size:37.5px;color:red;padding:0 4px;border:2px solid white;border-radius:50px}.giftpinned-container .carousel-background-container{width:100%;padding:30px 0;height:auto;background-color:white;border-radius:5px}.giftpinned-container .carousel-background-container .active,.giftpinned-container .carousel-background-container .inactive{width:40px;height:5px;border-radius:30px;background-color:#cecece;margin:0 5px;cursor:pointer}.giftpinned-container .carousel-background-container .active{background-color:black}.giftpinned-container .carousel-background-container .carousel-without-dot{width:100%}.giftpinned-container .carousel-background-container .carousel-with-dot{width:100%;padding-bottom:45px}


.App{height:auto;overflow:hidden}.App .Content{width:100vw;display:grid;place-items:center;padding-bottom:100px}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

